.shopon_bio .active .animated_btn_bg{
    width: 46%;
}
.container-shopon{
    padding-left: 0;
    padding-right: 0;
}
.shopon_bio .aside__ .a_footer_.a_header_ .card-body{
    // height: calc(100% - 65px);
}
.scale_preview_wrap{    
    max-width: 270px;
    height: 554px;
    width: 100%;
    overflow: hidden;
    border: 14px solid var(--bs-gray-400);
    border-radius: 40px;
    transition: all .3s ease;
}
.shopon_bio .p_header_ .btn-primary{
    background-color: rgba(var(--bs-primary-rgb),.05);
    color: var(--bs-primary);
    border-color: var(--bs-primary);
}
.shopon_bio .p_header_ .btn-outline-primary{
    background-color: var(--bs-body-bg);
    color: var(--bs-dark);
    border-color: var(--bs-gray-500);
}
.scale_preview{
    padding-bottom: 205%;
    border-radius: 22px;
    overflow: hidden;
}
.scale_preview iframe{
    position: absolute;
    top: -14.6%;
    left: -22%;
    width: 145%;
    height: 135%;
    transform: scale(0.7);
    border: 0;
    margin: 0;
    border-radius: 35px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

.laptop_active.scale_preview_wrap{
    max-width: 800px;
}


.laptop_active .scale_preview {
    padding-bottom: 68.1%;
}

.laptop_active .scale_preview iframe{
    top: -21.4%;
    left: -22%;
    width: 145%;
    height: 143%;
}
.loader__{
    height: 100% !important;
    left: 50% !important;
    top: 50% !important;
    transform: translate(-50%, -50%);
}
@media (max-width: 1600px){
.scale_preview_wrap {
    max-width: 265px;
    height: 540px;
}

.laptop_active.scale_preview_wrap {
    max-width: 680px;
}

.scale_preview_wrap{
    border: 12px solid var(--bs-gray-400);
}
.laptop_active .scale_preview {
    padding-bottom: 79%;
}

}